.blogContainer {
  margin-top: 81px;

  @media screen and (max-width: 991px) {
    margin-top: 58px;
  }
}

.blogContent {
  max-width: 1200px;
  padding: 15px;
  width: 100%;
  margin: auto;
  margin-top: 100px;
  margin-bottom: 100px;

  h1 {
    font-weight: 800;
    font-size: 40px;
    line-height: 49px;
    color: #3A8195;
  }

  h2 {
    font-weight: 600;
    font-size: 24px;
    line-height: 24px;
    color: #374A59;
  }

  [class*="date"] {
    font-weight: 300;
    font-size: 14px;
    color: #374A59;
    margin-top: 20px;
    margin-bottom: 15px;
  }

  [class*="seperator"] {
    width: 106px;
    height: 7px;

    background: #88DBA8;
    border-radius: 3.5px;
    margin-bottom: 70px;
  }
}